@media  screen and (max-width: 850px) {
    header {
        min-height: 10vh;
    }
    #hello-tag {
        height: 200px;
        width: 300px;
    }
    #hello {
        font-size: 40px;
        bottom: 50px   
    }
    #tag-text {
        font-size: small;
        bottom: 102px
    }
    h1 {
        font-size: 35px;
    }

    nav {
        font-size: 12px;
        min-height: 2.5vh;
        margin-top: 10px;
        width: 96%;
    }
    h2 {
        font-size: 45px;
    }
    #intro {
      flex-direction: column;
      align-items: center;
      justify-content: center;
    }
    #intro img {
        margin-top: 30px;
        max-height: 300px;
        max-width: 200px;
        
    }
    #bio {
      margin-top:0 ;
    }
    #bio p {
      margin-top: 20px;
        font-size: 12px;
    }

    h3 {
        font-size: 20px;
    }
    h4 {
        font-size: 18px;
    }
    
    .project-info {
      flex-direction: column;
      align-items: center;
      text-align: center;
    }
    .project{
      flex-direction: column;
        align-self: center;
        margin: 0 auto;
        align-items: center;
        max-width: 80vw;
        font-size: 13px;
    }
    .desc {
        width: 100%;
    }
    .tech {
        width: 125px;
    }
    #webapps img, #game-projects img, #mobile-projects img{
        max-height: 100px;
        box-shadow: 0 0 10px black;
        margin-left: 20px;
      }
      iframe {
        height: 100px;
        width: 177px;
      }
      #mobile-projects img {
        margin-left: 20px;
      }
      #contact {
        flex-direction: column;
        align-items: center;
      }
      #contact, .footerlinks {
        font-size: 12px;
      }

      #foot {
        margin-top: 20px
      }

      .fimage {
        max-height: 30px;
        margin: 3px, 10px
      }
      .footerlinks {
        margin: 20px 20px;
      }
      #links {
        flex-wrap: wrap;
        justify-content: center;
        margin-bottom: -25px;
      }
      .footnote {
        font-size: 12px;
      }
}

