@media  screen and (max-width: 1300px) {
    header {
        min-height: 10vh;
    }
    #hello-tag {
        height: 200px;
        width: 300px;
    }
    #hello {
        font-size: 40px;
        bottom: 50px   
    }
    #tag-text {
        font-size: small;
        bottom: 102px
    }
    h1 {
        font-size: 35px;
    }

    nav {
        font-size: 12px;
        min-height: 2.5vh;
        margin-top: 10px;
        width: 96%;
    }
    h2 {
        font-size: 70px;
    }
    #intro {

    }
    #intro img {
        margin-top: 60px;
        max-height: 300px;
    }
    #bio p {
        font-size: 16px;
    }

    h3 {
        font-size: 30px;
    }
    h4 {
        font-size: 22px;
    }
    .project{
        align-self: center;
        margin: 0 auto;
        align-items: center;
        max-width: 80vw;
        font-size: 13px;
    }
    .desc {
        width: 500px;
    }
    .tech {
        width: 125px;
    }
    #webapps img, #game-projects img, #mobile-projects img{
        max-height: 100px;
        box-shadow: 0 0 10px black;
        margin-left: 20px;
      }
      iframe {
        height: 100px;
        width: 177px;
      }
      #mobile-projects img {
        margin-left: 20px;
      }
      #contact {
        flex-direction: column;
        align-items: center;
      }
      #contact, .footerlinks {
        font-size: 12px;
      }

      .fimage {
        max-height: 60px;
        margin: 3px, 10px
      }
      .footerlinks {
        margin: 20px 20px;
      }
      .footnote {
        font-size: 12px;
      }
}

